@media (max-width: 1024px) {
  * {
    width: 100%; /* 노트북이 작은 화면일 때 이미지 너비 조정 */
    height: auto; /* 높이를 자동으로 조정하여 비율 유지 */
    margin: 0;
  }
}

@media (max-width: 768px) {
  * {
    width: calc(100%); /* 노트북이 더 작은 화면일 때 이미지 너비 조정 */
    height: auto; /* 높이를 자동으로 조정하여 비율 유지 */
    font-size: 20px;
    margin: 0;
  }
}
@media (max-height: 375px) {
  * {
    width: auto; /* 노트북이 더 작은 화면일 때 이미지 너비 조정 */
    height: auto; /* 높이를 자동으로 조정하여 비율 유지 */
    font-size: 16px;
    margin: 0;
  }
}

@media (min-width: 1024px) {
  h1 {
    font-size: 3.5rem;
  }

  h2 {
    font-size: 2.5rem;
  }

  a {
    font-size: 1.5rem;
  }

  p {
    font-size: 1.5rem;
  }

  blockquote {
    font-size: 2.5rem;
  }

  .hero {
    font-size: 3.5rem;
  }
}

@keyframes fadeInUp{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@media screen and (min-width: 768px) {
  .menu-icon {
      display: none;
  }

  .gnball {
      display: flex;
  }
}


