.App {
  text-align: center;
  background-color: #000000;
  overflow-y: auto;
  height: calc(var(--vh, 1vh) * 100);
}
/* 스크롤 바 */
.App::-webkit-scrollbar {
  display: none;
}

/* GNB */
.gnb {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 99999;
  top: 0;
  width: 90%;
  padding: 0 0 0 40px;
}

.logo-container {
  display: flex;
  align-items: center;
  font-size: 24px;
  font-weight: 700;
}

.gnb img, .gnb>a {
  text-decoration: none;
  width: 40.95px;
  margin: 1rem;
}

.gnb:hover{
  transition: 0.5s ease-out;
  background-color: rgba(0, 0, 0, 0.5);
}

.gnb ul {
  display: flex;
}

.gnb ul li a{
  font-weight: 700;
  display: block;
  text-decoration: none;
  padding: 0.5rem;
  margin: 0 0.5rem;
  border-radius: 0.5rem;
}
.gnb ul li a:hover{
  color: #7F2BE2;
}

.joinbtn{
  color: #FFFFFF;
  font-size: 20px;
  width: 120px;
  background-color: transparent;
  border-color: #7F2BE2;
  color: #7F2BE2;
  border-radius: 20px;
  border: #7F2BE2 3px solid;
  padding: 6px 15px;
  cursor: pointer;
}

.joinbtn:hover { 
  transition: scale 2s; 
  transform: scale(1.1);
}

.gnb .menu{
  position: absolute;
  top: 1rem;
  right: 0.2rem;
  flex-direction: column;
  justify-content: space-between;
  width: 2rem;
  height: 1.5rem;
}

.gnb .menu span{
  height: 0.2rem;
  width: 100%;
  background-color: #fff;
  border-radius: 0.2rem;
}

@media (max-width: 912px){
  .gnb .menu{
    display: flex;
    margin-top: 2vh;
  }

  .gnb{
    flex-direction: column;
    align-items: start;
    z-index: 9999;
    margin: 0;
    width: 100%;
    padding: 0;
  }

  .gnb ul{
    display: none;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0.25rem;
  }

  .gnb ul.open{
    display: flex;
  }

  .gnb ul li{
    width: 100%;
    text-align: center;
  }

  .gnb ul li a{
    margin: 0.2rem 0.5rem;
  }
}


/* Frist */

@media (max-width: 768px) {
  .Mainimg{
    display: none;
  }
  .maintext > .ITContainer{
    margin-top: 10vh;
  }
  .maintext > .ITContainer,
  .maintext>.ITContainer>.IT>span,
  .maintext>.ITContainer>.IT {
    line-height: 20px;
    font-size: 12px;
    white-space: normal;
    display: block;
  }
  .maintext > .Need,
  .maintext > .Need > .insighttext{
    font-size: 44px;
  }
  .maintext > .Need > .insighttext{
    font-size: 66px;
    line-height: 50px;
  }
}


.Mainimg {
  max-width: 100%;
  max-height: auto;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  overflow-y: auto;
}

.maincontent{
  position: absolute;
}

.maintext{
  position: relative;
  color:white;
  top: 30%;
  right: 2%;
  cursor: default;
  display: block;
}

.Need, .insighttext{
  font-size: 80px;
  cursor: default;
  animation: fadeInUp 3s;
}
.IT{
  font-size: 23px;
  cursor: default;
  animation: fadeInUp 3s;
}

.insighttext {
  color:#7F2BE2;
}

.mainscroll{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 55vh;
}

.scroll{
  width: 139px;
  height: 20px;
  border-radius: 20px;
  background-color: #fff;
  color: #000000;
  font-size: 12px;
}

